*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
  --scaleFactor: 1;
  --bigTextSize: calc(var(--scaleFactor)*2vw);
  --mediumTextSize: calc(var(--scaleFactor)*1.75vw);;
  --smallTextSize: calc(var(--scaleFactor)*1.5vw);
  display: flex;
}
.App{
  align-items: center;
  text-align: center;
}
